export default {
  name: 'Register',
  meta: {
    isClosed: true
  },
  head: {
    title: '注册',
    titleTemplate: '%s - DJL箫氏的博客!',
    htmlAttrs: {
      lang: 'en',
      amp: true
    },
    meta: [
      {
        hid: 'description',
        name: 'description',
        content: '这是DJL箫氏的博客的注册页'
      }
    ]
  },
  data() {
    const checkEmail = (rule, value, callback) => {
      value = value.trim();
      const regexp = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (!regexp.test(value)) {
        callback(new Error('请输入正确的邮箱地址'));
      } else {
        callback();
      }
    };

    // const checkEmailExsit = (rule, value, callback) => {
    //   value = value.trim();
    //   const { xhrInstance } = this.$http({
    //     url: '/getUserBy',
    //     data: {
    //       userMsg: value,
    //       getUserMethod: 'USER_EMAIL'
    //     }
    //   });

    //   xhrInstance.then((res) => {
    //     if (res) {
    //       callback(new Error('邮箱已存在'));
    //     } else {
    //       callback();
    //     }
    //   });
    // };

    return {
      form: {
        username: '',
        password: '',
        email: ''
      },
      rules: {
        username: [
          { required: true, trigger: 'blur', message: '请输入用户名' }
        ],
        password: [{ required: true, trigger: 'blur', message: '请输入密码' }],
        email: [
          { required: true, trigger: 'blur', message: '请输入邮箱' },
          { trigger: 'blur', validator: checkEmail }
          // { trigger: 'blur', validator: checkEmailExsit }
        ]
      }
    };
  },
  methods: {
    register() {
      const { response } = this.$http({
        method: 'post',
        url: '/user',
        data: {
          username: this.form.username,
          password: this.form.password,
          email: this.form.email
        },
        showErrorMsg: true,
        showSuccessMsg: true
      });

      response.then(
        () => {
          this.goToLogin();
        },
        () => {}
      );
    },
    formValidate() {
      if (this.$refs.form) {
        this.$refs.form.validate((valid) => {
          if (valid) {
            this.register();
          }
        });
      }
    },
    goToLogin() {
      this.$router.push('/login');
    },
    goToHomePage() {
      this.$router.push('/');
    }
  }
};
